@charset "UTF-8";
/*引入基本样式*/
/*把px转换为rem*/
* {
  margin: 0;
  padding: 0;
}
ul, ol {
  list-style: none;
}
a {
  text-decoration: none;
}
.topiclist {
  margin-top: 0.66667rem;
}
.items {
  width: 100%;
}
.items .item {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 1.33333rem;
    border-bottom: 1px solid #ccc;
    padding: 0 0.10667rem;
}
.items .item .img {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
}
.items .item .img img {
        display: block;
        -ms-flex-item-align: center;
            align-self: center;
        width: 0.8rem;
        height: 0.8rem;
}
.items .item .top {
      margin: 0 0.18667rem;
      -ms-flex-item-align: center;
          align-self: center;
      width: 0.86667rem;
      height: 0.53333rem;
      background: #e5e5e5;
      color: #999;
      font-size: 0.29333rem;
      text-align: center;
      line-height: 0.53333rem;
      border-radius: 0.10667rem;
}
.items .item .title {
      width: 7.33333rem;
      color: #333;
      font-size: 0.37333rem;
      line-height: 1.33333rem;
      /*处理文本超出容器,超出的部分用 ... 代替*/
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
}
.items .item .topStyle {
      background: #369614;
      color: #fff;
}

